@import "../../../styles/variables.scss";

.SocialNetwork {
  height: 2.25rem;
  width: 2.25rem;
  padding: 0.75rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  background-color: $text;
  color: $bg;
  & > * {
    height: 2rem;
    width: 2rem;
    transition: 0.5s;
    transition-delay: 0s;
  }
  transition: 0.5s;

  &:hover {
    background-color: $black;
    color: $element;
  }
}
